<template>
    <div class="kechengneirong">
        <div class="title">{{kecheng.title}}</div>
        <div v-html="kecheng.content"></div>
    </div>
</template>

<script>
    import api from "@/api/index";
    export default {
        created(){
            this.neirongid = this.$route.query.id;
            this.getKecheng();
        },
        data() {
            return {
                neirongid: '',
                kecheng: {},
            }
        },
        methods: {
            async getKecheng() {
                // let userSpecialInfo = wepy.getStorageSync(USER_SPECICAL_INFO) || {};
                // let openid = userSpecialInfo.openid;
                // if (typeof openid == "undefined") {
                //     wepy.navigateTo({
                //         url: "/pages/shouquan"
                //     });
                //     return false;
                // }
                let json = await api.getKecheng({
                        // openid: openid,
                    neirongid: this.neirongid,
                });
                this.kecheng = json.data;
            },
            onShareAppMessage() {
                return {
                    title: "",
                    path: "/pages/home"
                };
            },
            onLoad(e) {
                this.neirongid = e.neirongid;
                this.getKecheng();
            },
        }
    }
</script>

<style lang="less">
    .kechengneirong {
        background: #ffffff;
        padding-left: 10px;
        padding-right: 10px;
        color: #333;
        word-break: break-all;
        text-align: justify;
        padding-bottom: 100px;
    }
    .title {
        color: #333;
        font-size: 30px;
    }
</style>



